}
.midTemp1Right-box{
/*	background-color: blue;*/
}

.midTemp1-cover h1{
	font-size: 5vw;
	font-weight: bold;
	font-family: 'Poppins',sans-serif;
	margin-bottom: 0px;
}
.midTemp1-cover h2{
	font-size: 2vw;
	font-family: 'Poppins',sans-serif;
}
.midTemp1-cover h4{
	font-weight: 600;
	font-size: 2.2vw;
	border-bottom: .1vw solid black;
	margin-top: 2vw;
	padding-bottom: .2vw;
}
.midTemp1-cover h5{
	font-size: 2vw;
	margin: 0px;
}
.midTemp1-cover span{
	font-weight: bold;
	font-weight: 500;
	font-size: 1.7vw;

}
.midTemp1-cover p{
	font-size: 1.6vw;
	text-align: justify;
}
.midTemp1-cover ul li{
	font-size: 1.6vw;
	padding: 0px;
}
.midTemp1-cover table tr td{
	font-size: 1.6vw;
}
.midTemp1-cover .contact-list{
	padding: 0px;
	list-style: none;
}
.midTemp1-cover .contact-list li{
	margin-bottom: .2vw;
}
.midTemp1-cover .contact-list .contact-list-icons{
	margin-right: .7vw;
}

.midTemp1-cover .skills-list{
	padding-left: 1.5vw;
}

.midTemp1-cover .interest-list{
	padding-left: 1.5vw;
}

.midTemp1-cover .education-list-icons{
	margin-right: .5vw;
}



/*media query*********************************************************************/
